html {
  font-style: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

body {
  font-family: "Poppins", sans-serif;
  position: relative;
  overflow-x: hidden;
}

.card {
  background-color: salmon;
}

.btn {
  border: none;
  height: 45px;
  width: 8.25rem;
  border-radius: 6.25rem;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
}
.btn-primary {
  background: linear-gradient(90deg, #FF7A85 0%, #FF525D 100%);
  color: #ffffff;
}
.btn-white {
  background: #ffffff;
  color: #FF525D;
  transition: 0.3s;
}
.btn-white:hover {
  background: #FF7A85;
  color: #ffffff;
}
.btn-border {
  background: transparent;
  color: #ffffff;
  border: 0.125rem solid #ffffff;
  transition: 0.3s;
}
.btn-border:hover {
  background: #ffffff;
  color: #FF525D;
}

.atf {
