.nav {
  width: 100%;
  height: fit-content;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  max-width: 81.25rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  z-index: 2;
  justify-content: space-between;
  padding-top: 1.25rem;
  flex: 5;
}
@media (max-width: 1400px) {
  .nav {
    max-width: 75rem;
  }
}
@media (max-width: 1200px) {
  .nav {
    max-width: 56.25rem;
  }
}
@media (max-width: 960px) {
  .nav {
    max-width: 43.75rem;
  }
}
@media (max-width: 720px) {
  .nav {
    max-width: 21.25rem;
    top: 0.625rem;
  }
}
.nav__left {
  display: flex;
  align-items: center;
  gap: 3.125rem;
  position: relative;
}
@media (max-width: 960px) {
  .nav__left {
    gap: 1.875rem;
  }
}
@media (max-width: 720px) {
  .nav__left {
    width: 100%;
    justify-content: space-between;
  }
}
.nav__left__logo {
  width: 6.25rem;
}
.nav__left #menuToggle {
  display: none;
  width: 2.5rem;
  height: 100%;
}
@media (max-width: 720px) {
  .nav__left #menuToggle {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
}
.nav__left #menuToggle input {
  position: absolute;
  display: block;
  cursor: pointer;
  width: 2.5rem;
  height: 1.875rem;
  top: 0.3125rem;
  z-index: 2;
  opacity: 0;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}
.nav__left #menuToggle input:focus {
  outline: none;
}
.nav__left #menuToggle input:checked + .hamburger span {
  transform: rotate(45deg) translate(-2px, -1px);
}
.nav__left #menuToggle input:checked + .hamburger span:nth-child(2) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}
.nav__left #menuToggle input:checked + .hamburger span:nth-child(3) {
  transform: rotate(-45deg) translate(0, -1px);
  top: 0.1875rem;
}
.nav__left #menuToggle .hamburger span {
  display: block;
  width: 2.125rem;
  height: 0.25rem;
  margin-bottom: 0.3125rem;
  position: relative;
  background-color: #ffffff;
  background-repeat: 0.1875rem;
  transform-origin: 0.25rem 0;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}
.nav__left #menuToggle .hamburger span:first-child {
  transform-origin: 0% 0%;
}
.nav__left #menuToggle .hamburger span:last-child {
  transform-origin: 0% 100%;
}
.nav__left__menu {
  display: grid;
  grid-template-columns: repeat(3, minmax(80px, 1fr));
  gap: 2.5rem;
  font-family: "Ubuntu", sans-serif;
  font-size: 1rem;
  height: 100%;
}
@media (max-width: 1200px) {
  .nav__left__menu {
    gap: 1.5625rem;
  }
}
@media (max-width: 960px) {
  .nav__left__menu {
    gap: 1.25rem;
  }
}
@media (max-width: 720px) {
  .nav__left__menu {
    display: none;
  }
  .nav__left__menu.show {
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: #ffffff;
    height: fit-content;
    width: 100%;
    padding: 1.875rem;
    align-items: center;
    top: 3.75rem;
    box-shadow: 0px 14px 24px rgba(0, 0, 0, 0.25), 0px 1.75302px 3.00517px rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
  }
  .nav__left__menu.show li.dropdown {
    height: auto;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 1.25rem;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
  }
  .nav__left__menu.show li.dropdown:focus {
    outline: none;
  }
  .nav__left__menu.show li.dropdown.active .dropdown__button:after {
    transform: rotate(180deg);
  }
  .nav__left__menu.show li.dropdown.active .dropdown__content {
    display: flex;
    position: relative;
    align-items: center;
    top: 0;
    left: 0;
    background: #EFEFF1;
    box-shadow: none;
    padding: 1.25rem 0;
    gap: 1.25rem;
    width: 100%;
  }
  .nav__left__menu.show li.dropdown .dropdown__button {
    color: #4B5862;
    width: fit-content;
  }
  .nav__left__menu.show li.dropdown .dropdown__button:after {
    top: -0.125rem;
    height: 100%;
    content: url(assets/images/icon-arrow-dark.svg);
  }
  .nav__left__menu.show li.dropdown:hover .dropdown__button {
    text-decoration: none;
    font-weight: normal;
  }
  .nav__left__menu.show li.dropdown:hover .dropdown__button:after {
    transform: rotate(none);
  }
}
.nav__left__menu li {
  cursor: pointer;
  z-index: 50;
}
.nav__left__menu li.dropdown {
  z-index: 5;
  height: 100%;
  display: flex;
}
.nav__left__menu li.dropdown .dropdown__button {
  background-color: transparent;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  display: block;
  text-align: center;
  position: relative;
}
.nav__left__menu li.dropdown .dropdown__button:after {
  position: absolute;
  right: -0.9375rem;
  bottom: 0.9375rem;
  content: url(assets/images/icon-arrow-light.svg);
  transition: all 0.3s ease;
}
.nav__left__menu li.dropdown .dropdown__content {
  display: flex;
  flex-direction: column;
  display: none;
  box-shadow: 0px 17px 22px rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
  padding: 2.5rem 4.375rem 1.875rem 1.875rem;
}
.nav__left__menu li p:hover {
  text-decoration: underline;
}
.nav__left__menu:not(.show) li.dropdown:hover {
  position: relative;
}
.nav__left__menu:not(.show) li.dropdown:hover .dropdown__button {
  text-decoration: underline;
  font-weight: 700;
}
.nav__left__menu:not(.show) li.dropdown:hover .dropdown__button:after {
  transform: rotate(180deg);
}
.nav__left__menu:not(.show) li.dropdown:hover .dropdown__content {
  display: block;
  position: absolute;
  background: #fff;
  z-index: 999;
  top: 2.8125rem;
  left: -2.8125rem;
}
.nav__left__menu:not(.show) li.dropdown:hover .dropdown__content a {
  display: block;
  margin-bottom: 1.25rem;
}
.nav__left__menu:not(.show) li.dropdown:hover .dropdown__content a:hover {
  text-decoration: underline;
}
.nav__left__menu hr {
  width: 100%;
  border-top: #d1d1d1;
}
.nav__right {
  display: flex;
  align-items: center;
  gap: 1.875rem;
}
@media (max-width: 960px) {
  .nav__right {
